<template>
  <div id="app">
    <transition name="fade-in">
      <div v-if="!loading && !error" class="main">
        <sidebar-view />
        <main>
          <header-view />
          <router-view />
        </main>
      </div>
      <global-error v-if="error" />
    </transition>
    <div v-if="loading">
    </div>
  </div>
</template>

<script>
import HeaderView from '@/layouts/Header';
import SidebarView from '@/layouts/Sidebar';

import GlobalError from '@/components/GlobalError';

export default {
  name: 'App',
  components: {
    HeaderView,
    SidebarView,
    GlobalError,
  },
  head() {
    return {
      link: [
        {
          rel: 'stylesheet',
          href:
            'https://fonts.googleapis.com/css?family=Montserrat:600|Open+Sans:300,400,600',
        },
        {
          rel: 'stylesheet',
          href:
            'https://cdnjs.cloudflare.com/ajax/libs/highlight.js/9.9.0/styles/default.min.css',
        },
      ],
    };
  },
  data() {
    return {
      loading: true,
    };
  },
  watch: {
    definition() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
  },
  computed: {
    definition() {
      return this.$store.state.Schema.current;
    },
    error() {
      return this.$store.state.error;
    },
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      // this.$store.dispatch('Schema/fetch', {
      //   url: '/static/openapi3/petstore.yaml'
      // });
      // this.$store.dispatch('Schema/fetch', {
      //   url: '/static/swagger2/fullspec.json'
      // });
      // this.$store.dispatch('Schema/fetch', {
      //   url: '/static/swagger2/petstore.json'
      // });
      // this.$store.dispatch('Schema/fetch', {
      //   url: '/static/swagger2/slack.yaml'
      // });
      this.$store.dispatch('Schema/fetch', {
        url: '/static/swagger2/instagram.yaml',
      });
      // this.$store.dispatch('Schema/fetch', {
      //   url: '/static/swagger2/uber.json'
      // });
    },
  },
};
</script>

<style lang="scss">
@import '~@/styles/vars.scss';

#app {
  font: $font-stack1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: $color-base;
  background: #f4f7fa;
}
.main {
  display: grid;
  grid-template-columns: 250px auto;
  min-height: 100vh;
}

@import '~@/styles/globals.scss';
@import '~@/styles/transitions.scss';
</style>



